body {
  font-family: sans-serif;
}
.App-header {
  display: flex;
  align-items: center;
  color: #cd4444;
  border-bottom: 3px solid #cd4444;
}

.App-header img {
  width: 200px;
  height: 200px;
}

.App-body {
  min-height: calc(100vh - 530px);
  padding: 2em;
}

.App-footer {
  border-top: 3px solid #cd4444;
  text-align: center;
  font-style: italic;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

label,
input {
  margin-right: 10px;
}
